.home {
    display: flex;
    justify-content: center;
    margin-left: auto;
    margin-right: auto;
    max-width: 1500px;
}

.home_image {
    width: 100%;
    z-index: -1;
    margin-bottom: -150px;
    mask-image: linear-gradient(to bottom, rgba(0, 0, 0, 1), rgba(0, 0, 0, 0));
}

.home__row {
    display: flex;
    z-index: 1;
    margin-left: 5px;
    margin-right: 5px;
}


/* MEDIA QUERY */
@media screen and (max-width: 940px) {
    .home {
        margin-top: -10px;
    }
    .home__row {
        flex-direction: column;
        margin-left: 5px;
        margin-right: 5px;
        margin-top: 60px;
    }
}