.product {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: flex-end;
    margin: 10px;
    padding: 20px;
    width: 100%;
    max-height: 400px;
    max-width: 100%;
    background-color: #ffffff;
    z-index: 1;
}

.product > img {
    max-height: 200px;
    width: 100%;
    object-fit: contain;
    margin-bottom: 15px;
}

.product_info {
    height: 100px;
    margin-bottom: 15px;
}

.product_price {
    margin-top: 5px;
}

.product > button {
    background: #f0c14b;
    border: 1px solid;
    margin-top: 10px;
    border-color: #a88734 #9c7e31 #846a29;
    cursor: pointer;
    color: #111;
}

.product_rating {
    display: flex;
}

.starIcon {
    color: #ffa41c;
}


/* MEDIA QUERY */
@media screen and (max-width: 940px) {
    .product {
        width: 85%;
        margin: 10px;
        padding: 15px;
    }
    .product > img {
        max-height: 150px;
        margin-bottom: 10px;
    }
    .product__info > p {
        font-size: 10px;
    }
    .product > button {
        margin: 8px;
    }
}