.header {
    height: 60px;
    display: flex;
    align-items: center;
    background: #131921;
    position: sticky;
    top: 0;
    z-index: 100;   
}

.header__logo {
    width: 100px;
    object-fit: contain;
    margin: 0 20px;
    margin-top: 18px;
}

.header__search {
    display: flex;
    align-items: center;
    flex: 1;
    border-radius: 24px;
}

.header__searchInput {
    height: 12px;
    padding: 10px;
    border: none;
    width: 100%;
}

.header_searchIcon {
    padding: 5px;
    height: 22px !important;
    background: #cd9042;
}

.header__nav {
    display: flex;
    justify-content: space-evenly;
}

.header__option {
    display: flex;
    flex-direction: column;
    margin-left: 10px;
    margin-right: 10px;
    color: #fff;
}

.header_optionLineOne {
    font-size: 10px;
}
.header_optionLineTwo {
    font-size: 13px;
    font-weight: 800;
}

.header__optionBasket {
    display: flex;
    align-items: center;
    color: #fff;
}

.header__basketCount {
    margin-left: 10px;
    margin-right: 10px;
}



/* MEDIA QUERY */
@media screen and (max-width: 940px) {
    .header {
        height: 60px;
    }
    .header__logo {
        width: 60px;
        margin-top: 23px;
        margin-left: 10px;
    }
    .header__searchInput {
        height: 8px;
    }
    .header_searchIcon {
        padding: 5px;
        height: 19px !important;
    }
    .none {
        display: none;
    }
}